.timer h5{
    font-weight: 80;
    margin-bottom: 1px;
    margin-top: 3px;
    padding: 2px;
}

.education2{
    flex:1 1 35rem;
    margin-left: 5rem;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 3.5rem;
    padding-bottom: 3rem;
    border-left: .1rem solid #aaa;
    position: relative;
    animation: showText2 2s linear;
}
.education i{
    height: 4.5rem;
    width: 4.5rem;
    line-height: 4.5rem;
    font-size: 1.7rem;
    border-radius: 50%;
    background: crimson;
    color:aliceblue;
    text-align: center;
    position: absolute;
    top:-1rem; left:-2.5rem;
}
.education span{
    font-size: 1.2rem;
    color:aliceblue;
    border-radius: 5rem;
    padding:.5rem 1.5rem;
    background: #1a1a1a;
}
.education h3{
    font-size: 2rem;
    padding: 1rem 0;
    color:aliceblue;
    padding-top: 1rem;
    font-weight: normal;
}
.education p{
    font-size: .8rem;
    color:#aaa;
}

@keyframes showText{
    0%{
        transform: translateX(-100%) scale(1.5,1.5);
        text-shadow: none;
        color: transparent;
        opacity: 0;
    }
    100%{
        transform: translateX(0%) scale(1,1);
        text-shadow: 2px 2px 1px black;
        color: white;
        opacity: 1;
    }
}

@media only screen and (max-width: 850px){
    .AboutMe h1{
        font-size: 2rem;
    }
    .education h3{
        font-size: 1.5rem;
        padding: .5rem 0;
        padding-top: .5rem;
    }
    .education span{
        font-size: .7rem;
    }
    .profile-box {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto;
    }
}
